import comHeader from '@/components/header/header'

export default {
  name: 'new-post',
  components: {comHeader},
  computed: {
    hasUnsaved () {
      return this.title || this.content
    }
  },
  data () {
    return {
      title: '',
      content: ''
    }
  },
  beforeRouteLeave (to, from, next) {
    if (this.hasUnsaved) {
      this.$vux.confirm.show({
        content: '您确定要放弃本篇帖子吗？',
        onCancel () {
          next(false)
        },
        onConfirm () {
          next()
        }
      })
    } else {
      next()
    }
  },
  methods: {
    // 发布帖子
    addPost () {
      this.$router.push({name: 'post-release'})
    }
  }
}



